.slider {
  position: relative;
  height: 500px;
}

.slider .slick-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 40px;
  width: 80px;
  height: 80px;
  font-size: 0;
  background-color: #ffffff;
  box-shadow: 0 2px 15px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.slider .slick-next {
  left: auto;
  right: 40px;
}

.slider .slick-arrow:before, .slider .slick-arrow:after {
  content: '';
  position: absolute;
  top: 34px;
  left: 36px;
  display: block;
  width: 5px;
  height: 34px;
  background-color: #004f9f;
  transform: rotate(45deg);
  border-radius: 1px;
}

.slider .slick-arrow:hover {
  background-color: #004f9f;
}

